<template>
  <v-container>
    <v-layout>
      <v-col offset="1" cols="10">
        <v-card color="#f5efe6" class="py-2">
          <v-card-title class="pl-10"> </v-card-title>
          <v-card-actions class="pl-15">
            <v-btn @click="requestMyPhoto()">  </v-btn>
          </v-card-actions>
          <v-card-actions class="pl-15">
            <v-btn @click="requestMyRecipe()">  </v-btn>
            <v-btn @click="searchMyRecipe()">   </v-btn>
            <v-btn @click="sortMyRecipe()">   </v-btn>
          </v-card-actions>
          <v-card-actions class="pl-15">
            <v-btn @click="lookupMyLikeList()">  </v-btn>
          </v-card-actions>
          <v-card-actions class="pl-15">
            <v-btn @click="lookupMyCommentList()">  </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-layout>

    <v-layout>
      <v-col offset="1" cols="5">
        <v-card color="#f5efe6" class="py-2">
          <v-card-title class="pl-10"></v-card-title>
          <v-card-actions class="pl-15">
            <v-btn @click="requestRefri()"> </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>


      <v-col cols="5">
        <v-card color="#f5efe6" class="py-2">
          <v-card-title class="pl-10"></v-card-title>
          <v-card-actions class="pl-15">
            <v-btn @click="requestML()"> </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
      
    </v-layout>

    <v-layout>
      <v-col offset="1" cols="10" class="py-2">
        <v-card color="#f5efe6" class="py-2">
          <v-card-title class="ml-10"> </v-card-title>
          <v-card-actions class="ml-15">
            <v-btn @click="changeNi()"> </v-btn>
          </v-card-actions>
          <v-card-actions class="ml-15">
            <v-btn @click="changePW()"> </v-btn>
          </v-card-actions>
          
        </v-card>
      </v-col>
    </v-layout>


    <!-- <v-btn @click="changePW"> </v-btn> -->
  </v-container>
</template>

<style>

</style>

<script>
import router from '@/router/index.js';
import herokuAPI from '@/api/heroku.js';

export default {
  data() {
    return {
      myphotos: [],
      myrecipes: [],
    };
  },
  methods: {
    changePW() {
      router.push({
        path: "/email-auth/1",
      })
    },
    changeNi() {
      router.push({
        path: "/mypage/changeNickname",
      })
    },
    requestML() {
      router.push({
        path: "/mail"
      })
    },
    requestRefri() {
      router.push({
        path: "/mypage/refrigerator",
      })
    },
    requestMyPhoto() {
      let vm = this;
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      herokuAPI.myphotosLookup(UserInfo.nickname)
        .then(function(response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");
            for(let i = 0; response.data[i] != null; i++) {
              vm.myphotos.push(response.data[i]);
            }
          }
      })
    },
    requestMyRecipe() {
      let vm = this;
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      herokuAPI.myrecipesLookup(UserInfo.nickname)
        .then(function(response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");
            for(let i = 0; response.data[i] != null; i++) {
              vm.myrecipes.push(response.data[i]);
            }
          }
        })
    },
    searchMyRecipe() {
      let vm = this;
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      const SearchInfo = JSON.stringify({
        "nickname": UserInfo.nickname,
        "keyword": "test"
      });
      herokuAPI.myrecipesSearch(SearchInfo)
        .then(function(response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");
            for(let i = 0; response.data[i] != null; i++) {
              vm.myrecipes.push(response.data[i]);
            }
          }
        })
    },
    sortMyRecipe() {
      let vm = this;
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      const SortInfo = JSON.stringify({
        "nickname": UserInfo.nickname,
        "arrangeBy": " "
      });
      herokuAPI.myrecipesSort(SortInfo)
        .then(function(response) {
          console.log(" ", response);
          if(response.stats == 200) {
            console.log(" ");
            for(let i = 0; response.data[i] != null; i++) {
              vm.myrecipes.push(response.data[i]);
            }
          }
        })
    },
    lookupMyLikeList() {
      let vm = this;
      let postType = 1; /* 1=, -1= */
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      herokuAPI.mylikeposts(UserInfo.nickname, postType)
        .then(function(response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");
            if(postType == 1) for(let i = 0; response.data[i] != null; i++) vm.myrecipes.push(response.data[i]);
            else for(let i = 0; response.data[i] != null; i++) vm.myphotos.push(response.data[i]);
          }
        })
    },
    lookupMyCommentList() {
      let vm = this;
      const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
      herokuAPI.mycommentposts(UserInfo.nickname)
        .then(function(response) {
          console.log(" ", response);
          if(response.status == 200) {
            console.log(" ");
            for(let i = 0; response.data[i] != null; i++) vm.myrecipes.push(response.data[i]);
          }
        })
    }
  }
}
</script>