<template>
  <v-container>

    <v-row justify="center">
      <v-col class="col-xl-8 col-md-10">
        <v-card min-height="800" color="#f5efe6" class="pa-4">
          <div class="mailbox-title px-16 py-10">
            
          </div>

          <!--   v-for . -->
          <v-card min-height="100" color="#fefefe" class="my-5 mx-16" @click="showMail(index)" v-for="(val,index) in mails" :key="val.mail_id">
            <v-card-title primary-title class="mb-0">
              {{val.title}}
            </v-card-title>
            
            <div class="d-flex align-top justify-space-between mx-4" >
              <span>:{{val.nickname}}</span>
              <span>:{{val.receiver}}</span>
              <span>{{val.send_time}}</span>
            </div>

          </v-card>
        </v-card>

        <!--    -->
        <v-btn fab x-large color="#E8DFCA" class="write-btn">
          <v-icon color="#7895B2">mdi-plus</v-icon>
        </v-btn>

      </v-col>
    </v-row>

    <!--   -->
    <v-dialog
      max-width="500"
      v-model="LookupMail"
    >
      <lookup-mail
        :mailTitle=openMail.title
        :mailID=openMail.mail_id
        :mailSender=openMail.nickname
        :mailReceiver=openMail.receiver
        :sendTime=openMail.send_time
        :mailContents=openMail.contents
        btn1Title=""
        :btn2=false
        @hide="hideMail"
        @update="updateList"
      />
    </v-dialog>
    <!--   -->

  </v-container>
</template>

<style>

.mailbox-title{
  font-size: 2.3em;
}

.my-text{
  color:#7895B2;
}

.write-btn{
  position: fixed;
  bottom: 10%;
  right: 5%;
}

</style>

<script>
import herokuAPI from '@/api/heroku.js';
import router from '@/router/index.js';
import LookupMail from '@/components/lookupMail.vue';

export default{
  components: {
    LookupMail
  },
  data () {
    return {
      LookupMail: false,
      mails: [],
      openMail: {},
      total_page: null,
    }
  },
  mounted() {
    let vm = this;
    const UserInfo = JSON.parse(localStorage.getItem("UserInfo"));
    herokuAPI.mailList(UserInfo.nickname, 1)
      .then(function(response) {
        console.log("  ", response);
        if(response.status == 200) {
            console.log(" ");
            for(let i = 0; response.data.mailList[i] != null; i++) {
              vm.mails.push(response.data.mailList[i]);
            }
            vm.total_page = response.data.total_page;
          }
      })
  },
  methods: {
    showMail(index){ //  
      this.openMail = this.mails[index];
      this.LookupMail = true;
    },
    hideMail(){ //  
      this.LookupMail = false;
    },
    updateList(){ //   
      this.$router.go();
    },
    toCreate() {
      router.push({
        path: "/mail/create/",
      })
    },
  }
}
</script>